import { createAction } from '@reduxjs/toolkit'

export enum Field {
  INPUT = 'INPUT',
  OUTPUT = 'OUTPUT',
}

export const selectCurrency = createAction<{ field: Field; currencyId: string }>('biswap/selectCurrency')
export const switchCurrencies = createAction<void>('biswap/switchCurrencies')
export const typeInput = createAction<{ field: Field; typedValue: string }>('biswap/typeInput')
export const replaceSwapState = createAction<{
  field: Field
  typedValue: string
  inputCurrencyId?: string
  outputCurrencyId?: string
  recipient: string | null
}>('biswap/replaceSwapState')
export const setRecipient = createAction<{ recipient: string | null }>('biswap/setRecipient')
