import { createAction } from '@reduxjs/toolkit'

export enum Field {
  INPUT = 'INPUT',
  OUTPUT = 'OUTPUT',
}

export const selectCurrency = createAction<{ field: Field; currencyId: string }>('radioshack/selectCurrency')
export const switchCurrencies = createAction<void>('radioshack/switchCurrencies')
export const typeInput = createAction<{ field: Field; typedValue: string }>('radioshack/typeInput')
export const replaceSwapState = createAction<{
  field: Field
  typedValue: string
  inputCurrencyId?: string
  outputCurrencyId?: string
  recipient: string | null
}>('radioshack/replaceSwapState')
export const setRecipient = createAction<{ recipient: string | null }>('radioshack/setRecipient')
