import { createAction } from '@reduxjs/toolkit'

export enum Field {
  INPUT = 'INPUT',
  OUTPUT = 'OUTPUT',
}

export const selectCurrency = createAction<{ field: Field; currencyId: string }>('apeswap/selectCurrency')
export const switchCurrencies = createAction<void>('apeswap/switchCurrencies')
export const typeInput = createAction<{ field: Field; typedValue: string }>('apeswap/typeInput')
export const replaceSwapState = createAction<{
  field: Field
  typedValue: string
  inputCurrencyId?: string
  outputCurrencyId?: string
  recipient: string | null
}>('apeswap/replaceSwapState')
export const setRecipient = createAction<{ recipient: string | null }>('apeswap/setRecipient')
